@import 'footer';
@import 'navbar';
@import 'thankyou';
@import 'contact';
@import 'aboutme';
@import 'projects';


$first-color: #0C0032;						
$second-color: #464866;		
$third-color: #211A36;	
$contact-form: #edf5e1;
$main-font: Georgia, 'Times New Roman', Times, serif;
$hover:#659DBD;
$hover-menu:#FF0700;
$text-color:white;


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html, body {
    line-height: 1.5;
    font-size: 20px;
    font-family:$main-font;
    height: 100%;
    margin-left: 100px;
    padding: 0 20px;   
    background: $third-color;
  }

  @media screen and (max-width: 700px) {
    html, body {
      margin-left: 0;
      padding: 0;
    }
  }
  
  section h1 {
    font-size: 40px;
    text-align: center;
    padding: 10px;
    color: $text-color;						
  }

  section p {
    color: $text-color;	
    text-align: justify;
    hyphens: auto;
    -webkit-hyphens: auto;
    -webkit-hyphenate-limit-before: 3;
    -webkit-hyphenate-limit-after: 3;
    -webkit-hyphenate-limit-chars: 6 3 3;
    -webkit-hyphenate-limit-lines: 2;
    -webkit-hyphenate-limit-last: always;
    -webkit-hyphenate-limit-zone: 8%;
  }

  .container__horizontal {
    border: 2px solid $hover-menu;
    border-radius: 100%;
    width: 30%;
    margin: auto;  
  }

  @media screen and (max-width: 700px) {
    section h1 {
      font-size: 35px;
      padding: 0px;
				
    }
    .container__horizontal {
      width: 70%;
    }
  }

  li {
    list-style: none;
  }
  
  a {
    text-decoration: none;
  }


  





  
 
  
  
  .aboutme {
  	max-width: 1600px;
    padding: 20px 0;
}
  
  .aboutme p {
    text-align:justify;
    padding: 5px 0;
  }
  
  .aboutme__photo {
		margin: 0 40px 0 20px;
		float: left;
  }

  .aboutme__img {
    max-width: 300px;
    height: auto;
	  border-radius: 50%;
    filter: drop-shadow(1px 1px 20px $hover-menu);
  }
  
  @media only screen and (max-width: 900px) {
    .aboutme {
      display: flex;
      flex-direction: column-reverse;
      margin: 0 20px;
      padding: 10px 0;
    }

    .aboutme__text p { 
      padding-bottom: 10px;
    }

    .aboutme__photo {
      margin: auto;
      padding-top:20px;
     }
  }
  
  @media only screen and (max-width: 700px) {

    .aboutme__text p { 
        text-align: left;
   
    }
    .aboutme {
      margin: 0 25px;
      padding-bottom: 30px;

    }
  }


    

 









  .nav__header {
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-bottom: 50px;
    h3, p { 
      color: #ffffff;
      text-align: center;
      padding: 3px;
    }
  }

  .nav__header__logo {
    height:5rem;
    margin: 50px auto 40px;
  }
  
  .navbar {
    height: 100%;
    width: 200px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: $first-color;	
        ul {
        background-color: $second-color;
        display: block;
        text-align: center;
        padding: 1px 0;
        li {
            padding: 5px 0;
            margin: 37px 0;
                a {
                font-weight: bold;
                color: #ffffff;
                font-size: 25px;  
                    &:hover {
                    color: $hover-menu;
                }
            }
        }
    }
  }
  
  .drop__bar {
    display: none;
    .drop__bar__line1, .drop__bar__line2, .drop__bar__line3 {
        display: block;
        width: 25px;
        height: 4px;
        margin: 6px auto;
        background-color: white;
    }
  }

  .nav__socialmedia  { 
    text-align: center;
    padding: 25px;
  }

  .nav__socialmedia a { 
    display: inline-block;
    border-radius: 2px;
    color: #ffffff;
    margin:10px;
    font-size: 50px; 
  }

  .nav__socialmedia a:hover {
    transform:scale(1.1); 
    -webkit-transform:scale(1.1);
    color: $hover-menu;
  }

  @media screen and (max-width: 700px) {
    .nav__header{
      padding: 0px;
      h3 { 
      padding: 0px;
      }
      p {
      padding-bottom:0px;
      }
    }
  
  
    .nav__header__logo {
      height:5rem;
      margin: 40px auto 5px;
    }
    
    .navbar {
      width: 100%;
      position: relative;
      margin-top: -20px;
        ul {
          position: fixed;
          left: -150%;
          top: 267px;
          z-index: 1;
          flex-direction: column;
          background-color: $second-color;
          width: 100%;
          border-bottom-right-radius: 10px;
          border-bottom-left-radius: 10px;
          text-align: center;
          transition: 0.5s;
            li {
              margin: 5px;
                a {
                  font-size: 25px;
                  color: #ffffff;
                &:hover {
                  color: $hover-menu;           
              }
           }
        }
     } 
}
    
     .drop__bar {
          display: block;
          cursor: pointer;
          padding-bottom: 5px;
          margin: auto;
      }

      .drop__bar__line1, .drop__bar__line2, .drop__bar__line3 {
        transition: 0.4s;
      }

      .change .drop__bar__line1 {
        -webkit-transform: rotate(-45deg) translate(-9px, 9px) ;
        transform: rotate(-45deg) translate(-8px, 6px) ;
        height: 4px;
      }
      
      .change .drop__bar__line2 {
        opacity: 0;
        height: 4px;
      }
      
      .change .drop__bar__line3 {
        -webkit-transform: rotate(45deg) translate(-9px, -9px) ;
        transform: rotate(45deg) translate(-8px, -6px) ;
        height: 4px;
      }

      .nav__menu.active {
        left: 0;
      }
  
      .nav__socialmedia { 
        display:none; 
      }
    }
  


   






.contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0 40px;
}
 
.contact__verticalline {
      border: 2px solid $text-color;	;
      height: 500px;
      margin: 80px 100px 0 100px;
      border-radius: 100%;
    }

.contact__form {
  width: 37%;
  font-size: 22px;
  display: block;
}

input[type=text], textarea {
  display: flex;
  flex-direction: column;
  background-color: $hover;
  color: $text-color;
  width: 100%;
  padding: 12px;
  border-radius: 15px;
  margin-top: 6px;
  margin-bottom: 16px;
  font-size: 22px;
  font-family: $main-font;
  outline:none;
  resize: vertical;
}

input[type=text]:hover, textarea:hover {
  border: 2px solid $hover-menu;
}

input[type=submit] {
  background-color: $hover-menu;
  color: $text-color;
  padding: 10px 10px;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  font-weight: bold;
  font-size: 20px;
  font-family: $main-font;
  width: 30%;
  position: relative;
  left: 35%;
  text-align: center;
}

input[type=submit]:hover {
  background-color: lightcoral;
  filter: drop-shadow(1px 1px 10px red);
}

form > label {
  font-weight: bold;
  color: $text-color;
}

.contact__horizontal {
  display: none;
}

.contact__text {
  padding-top: 5px;
  display: grid;
  columns: 1;
  row-gap: 22px;
  text-align: center;
  font-size: 22px;
      p {
        text-align: center;
        font-family: 'Times New Roman', Times, serif;
      }
    }

.contact__text__phone__img1, .contact__text__email__img2 {
 height:4rem;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
.bounce:hover img {
  animation-name: bounce;
  cursor: pointer;
}

.contact__text__address {
  p:nth-child(2) {
    padding-top: 10px;
  }
}   

@media only screen and (max-width: 1100px) {
  .contact {
    flex-direction: column;
    padding: 10px 0 40px;
  }

  .contact__text {
    padding-top: 20px;
    row-gap: 30px;
    margin-bottom:-20px;
 }
 .contact__text__address {
  p:nth-child(2) {
    padding-top: 20px;
  } 
} 
  .contact__form {
    width: 90%;
    margin: auto;
    padding-bottom: 20px;
  }
  
  .contact__verticalline {
    display: none;
  }

  .contact__horizontal {
    display: inline-block;
    border: 2px solid white;
    border-radius: 100%;
    width: 70%;
    margin: 20px auto;
  }
  input[type=submit] {
    background-color: $hover-menu;
    color: $text-color;
    padding: 10px 10px;
    border: none;
    border-radius: 15px;
    cursor: pointer;
    font-weight: bold;
    font-size: 18px;
    font-family: $main-font;
    width: 30%;
    position: relative;
    left: 35%;
    text-align: center;
  }
}





























.projects {
  padding: 20px 0 20px;
  margin: auto;
}

.projects__text__intro {
  text-align: center justify;
  width: 80%;
  margin: auto;
  padding-bottom:30px;
}

.projects__portfolio {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 30px;
    column-gap: 60px;
    }

.projects__portfolio__item {
  background-color: $second-color;
  border-bottom-style: solid;
  border-bottom-color: $hover-menu;
  border-radius: 10px;
      h3 {
        text-align: center;
        padding-top: 10px;
        color: $text-color;
      }
      p {
        padding: 10px 20px;
        text-align: left;
      }
        a {
          color:$text-color;
          cursor: pointer;
          font-weight: bold;
          font-size: 40px;
          &:hover {
            transform:scale(1.4); 
            -webkit-transform:scale(1.5);
            color: $hover-menu;
        }
      }
    }

    .flip-box {
      max-width: 200px;
      height: 200px;
      cursor: pointer;
      margin: auto;
    }
    
    .flip-box-inner {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: center;
      transition: transform 0.8s;
      transform-style: preserve-3d;
    }
    
    .flip-box:hover .flip-box-inner {
      transform: rotateY(180deg);
    }
    
    .flip-box-front, .flip-box-front1, .flip-box-front2, .flip-box-back {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
    }
    
    .flip-box-front {
      background-color: #d6c6b4;
      z-index: 1; //so pointer work properly on the back side
    }

    .flip-box-front1 {
      background-color:#116466;
      z-index: 1; //so pointer work properly on the back side
    }

    .flip-box-front2 {
      background-color:$text-color;
      z-index: 1; //so pointer work properly on the back side
    }
    
    .flip-box-back {
      background-color: $hover;
      transform: rotateY(180deg);
      z-index: 2; //so pointer work properly on the back side
    }

    .flip__img1,  .flip__img2 {
      max-width: 70%;
      position: relative;
      top: 15%;
    }
    
    .animated img {
      animation-duration: 3s;
      animation-iteration-count: infinite;
    }
    
    .pulse img {
      animation-name: pulse;
      animation-duration: 3s;
    }

    #myImg1 {
      border-radius: 5px;
      transition: 0.3s;
    }

    .myImg1 {
      max-width: 90%;
      position: relative;
      top: 9%;
    }

    .myImg2 {
      max-width: 90%;
      position: relative;
      top: 14%;
    }

      .modal {
      display: none; 
      position: fixed; 
      z-index: 1;
      padding-top: 50px;
      left: 0;
      top: 0;
      width: 100%; 
      height: 100%; 
      overflow: auto; /* Enable to scroll */
      background-color: rgba(0,0,0,0.9); /* Black + opacity */
    }
    
    .modal-content {
      margin: auto;
      display: block;
      width: 100%;
      max-width: 950px;
    }
    
    #caption {
      margin: auto;
      text-align: center;
      color: $text-color;
      padding-top: 10px;
    }
    
    .modal-content, #caption {  
      -webkit-animation-name: zoom;
      -webkit-animation-duration: 0.6s;
      animation-name: zoom;
      animation-duration: 0.6s;
    }
    
    @-webkit-keyframes zoom {
      from {-webkit-transform:scale(0)} 
      to {-webkit-transform:scale(1)}
    }
    
    @keyframes zoom {
      from {transform:scale(0)} 
      to {transform:scale(1)}
    }
    
    .close {
      position: absolute;
      top: 15px;
      right: 35px;
      color: $text-color;
      font-size: 60px;
      font-weight: bold;
      transition: 0.3s;
      &:hover, &:focus {
        color: $hover-menu;
        text-decoration: none;
        cursor: pointer;
      }
    }


