.home {
  background-image: url(/img/background.png);		
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  margin: -17px -40px 20px;
  color: $text-color;
  font-weight: bold;
  display: block;
}

.home__name {
  color: $text-color;
  font-weight: bold;
  display: block;
  li:nth-child(1) {   
    padding: 30px 50px;
    text-align: right;
    a {
      color: $text-color;
      font-weight: bold;
      font-family:Georgia, 'Times New Roman', Times, serif
    }
    button {
      padding:10px;
      background-color: $hover-menu;
      border-radius: 10px;
    &:hover {
      background-color: lightcoral;
      filter: drop-shadow(1px 1px 10px red);
    }
  }
}
  li:nth-child(2) {
      overflow: hidden;
      white-space: nowrap;
      letter-spacing: 5px; 
      animation: typing 5s steps(100, end) both;
      font-size: 60px;
      margin: -30px auto auto;
      text-align: center;
    a {
      color: $text-color;
      font-size: 60px;
      font-weight: bold;
      letter-spacing: 5px; 
    &:hover {
      color: lightcoral;
      filter: drop-shadow(1px 1px 10px red);
    }
  }
}
  li:nth-child(3) {
        overflow: hidden;
        white-space: nowrap;
        letter-spacing: 5px; 
        animation: typing 5s steps(100, end) both;
        animation-delay: 2s;
        font-size: 40px;
        margin: -10px auto auto;
        text-align: center;
          }  
    li:nth-child(4) {
      overflow: hidden;
      white-space: nowrap;
      letter-spacing: 5px; 
      animation: typing 5s steps(100, end) both;
      animation-delay: 4s;
      font-size: 30px;
      padding: 30.5px 0 65px;
      margin: auto;
      text-align: center;
      font-style: italic;     
    }  
    li:nth-child(5) {   
      visibility: hidden;
      margin-top: -30px;
    }
  }
  

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}


@media only screen and (max-width: 1400px) {
  .home__name {
    li:nth-child(1) {   
      visibility: hidden;
      margin-top: -86px;
  }
  li:nth-child(2) {
    animation: typing 3s steps(100, end) both;
    margin: 10px auto auto;
}
li:nth-child(3) {
      animation: typing 3s steps(100, end) both;
      animation-delay: 2s;
        }  
  li:nth-child(4) {
    animation: typing 3s steps(100, end) both;
    animation-delay: 4s;
