        background-color: $hover-menu;
        border-radius: 10px;
      &:hover {
        background-color: lightcoral;
        filter: drop-shadow(1px 1px 10px red);
      }
}
}
}
}

@media only screen and (max-width: 1000px) {
  .home__name {
    li:nth-child(1) {   
      padding-top:40px;
 }
    li:nth-child(2) {
        animation: typing 2s steps(100, end) both;
      a {
        font-size: 45px;
        letter-spacing: 1px; 
    }
  }
    li:nth-child(3) {
          animation: typing 2s steps(100, end) both;
          animation-delay: 2s;
          font-size: 26px;
          letter-spacing: 1px; 

            }  
      li:nth-child(4) {
        animation: typing 2s steps(100, end) both;
        animation-delay: 4s;
        font-size: 24px;
        letter-spacing: 1px; 
        padding: 30px 0 31.5px;
      } 
      li:nth-child(5) {   
        padding: 41px 0;
            a {
              font-size: 10px;
              letter-spacing: 1px; 
            }
            button {
              padding:4px;
            } 
  }  
}
}


@media only screen and (max-width: 700px) {
  .home {
    margin: -17px auto 20px;
  }
  .home__name {
    li:nth-child(1) {   
      padding-top:10px;
 }
    li:nth-child(2) {
        animation: typing 2s steps(100, end) both;
        margin: auto;
      a {
        font-size: 40px;
    }
  }
    li:nth-child(3) {
          animation: typing 2s steps(100, end) both;
          animation-delay: 2s;
          font-size: 22px;

            }  
      li:nth-child(4) {
        animation: typing 2s steps(100, end) both;
        animation-delay: 4s;
        font-size: 20px;
        padding: 30px 0 31.5px;
      } 
      li:nth-child(5) {   
        padding: 30px 0;
  }  
}
}








